@import url(https://fonts.googleapis.com/css?family=Chivo&display=swap);
.scatterContainer {
  margin: 0 auto;
  text-align: center; }

.Axis path,
.Axis line {
  stroke: #f4f4f4;
}

.Axis text {
  font-size: 11px;
  fill: black;
}

.Axis-Bottom text {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  text-anchor: end;
}

.radialContainer, text {
  font-family: 'IBM Plex Sans';
  background-color: white; }

svg {
  margin-top: 60px; }

.center {
  text-align: center;
  margin: 0 auto;
  display: flex;
  justify-content: center; }

h3 {
  font-weight: 500;
  margin-bottom: 0; }

div.tooltip {
  position: absolute;
  text-align: center;
  width: 60px;
  height: 28px;
  padding: 2px;
  font: 12px sans-serif;
  background: lightsteelblue;
  border: 0px;
  border-radius: 8px;
  pointer-events: none; }

.menu {
  width: 100%;
  height: 100%;
  background-color: #565656;
  margin: 0;
  padding: 20px;
  color: white; }
  .menu .btnsHolder {
    margin-bottom: 20px; }
    .menu .btnsHolder .btn {
      padding: 5px 20px 5px 20px;
      margin: 0px 5px 0px 0px;
      font-size: 16px;
      border-radius: 2px; }
      .menu .btnsHolder .btn:hover {
        cursor: pointer; }
      .menu .btnsHolder .btn:focus {
        outline: none; }
    .menu .btnsHolder .btn-normal {
      border-color: #F5D800;
      color: #F5D800;
      background-color: #565656; }
    .menu .btnsHolder .btn-active {
      background-color: #F5D800;
      color: #565656;
      border-color: #F5D800; }

body {
  margin: 0;
  font-family: 'Chivo'; }

.app {
  display: flex;
  flex-flow: row wrap;
  flex-direction: row; }
  .app .menuContainer {
    width: 20%;
    height: 100vh; }
  .app .chartContainer {
    display: flex;
    align-items: center;
    width: 80%;
    height: 100%; }

